import {reactive} from 'vue'
/**
* {Object} pageInstanceState
* {Object} pageInstanceState.columnWidth -     
* {string} pageInstanceState.coefficient -      ( )
* {number} pageInstanceState.windowWidth -    
* {Array} pageInstanceState.currentColumnWidth -     
* {number|null} pageInstanceState.activeColumn -  ,     
* {string} pageInstanceState.language -  
* {Boolean} pageInstanceState.isLoading -    
* {Boolean} pageInstanceState.isLoaded -    .         
* {Boolean} pageInstanceState.lock -        
* {Array} pageInstanceState.content -   
* {Object} pageInstanceState.animation -  
* {Array} pageInstanceState.works -  
* {Array} pageInstanceState.activeWork -      
 * */
const pageInstanceState = reactive({
    columnWidth: {
        home: ["50%", "25%", "25%"],
        single: ["210px", "auto", "170px"],
        singleNews: ["210px", "auto", "200px"],
        about: ["auto", "170px"],
        gallery: ["220px", '43.5%', '43.5%'],
    },
    coefficient: 3,
    windowWidth: 1920,
    currentColumnWidth: [],
    activeColumn: [],
    activeBlock: null,
    language: "ru",
    isLoading: true,
    isLoaded: false,
    lock: false,
    content: [],
    animation: {
        done: false,
        delay: 600
    },
    works: [],
    activeWork: [],
    sections: [],
    newsSections: [],
    currentNewsSection: "",
    activeSection: "",
    activeNewsSection: {},
    activeWorkDescription: "",
    activeNewsDescription: "",
    totalToggler: " ",
    currentNewsPage: 1,
    currentColumnNewsPage: 1,
    isLoadingNews: false,
    firstNewsColumn: [],
    secondNewsColumn: [],
    newsColumn: [],
    maxNewsScrollTop: 0,
    newsItem: {}
})

//        resize
export function pageStateInit() {
    pageInstanceState.windowWidth = document.documentElement.clientWidth
    window.addEventListener('resize', () => {
        pageInstanceState.windowWidth = document.documentElement.clientWidth
    })
}


export default pageInstanceState
